import { atom } from 'recoil'
import { AccessTokenData } from '../types/permission'
import { ChannelJSON } from '../types/channel'

const storageChannel = localStorage.getItem('channel')
export const SelectedChannel = atom<ChannelJSON>({
  key: 'channel',
  default:
    storageChannel !== null
      ? JSON.parse(storageChannel)
      : {
          // id: '627b38a68a7b8f4a5a18613f',
          // name: 'PoRLIVE',
          // active: true,
          id: null,
          name: null,
          active: null,
        },
})

const storageAccessToken = localStorage.getItem('accessToken')
export const AccessToken = atom<string| null>({
  key: 'accessToken',
  default: storageAccessToken,
})

export const AccessData = atom<AccessTokenData| null>({
  key: 'accessData',
  default: null
})

const storageRefreshToken = localStorage.getItem('refreshToken')
export const RefreshToken = atom<string| null>({
  key: 'refreshToken',
  default: storageRefreshToken,
})
